.container {
  margin-top: 120px;
  height: calc(100vh - 120px);
  background-color: #fff;

  @media (max-width: 650px) {
    background-color: #fff;
    color: #000;

    margin-top: 0;
  }

  .content {
    width: 100%;
    height: calc(100vh - 120px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    h1 {
      font-weight: 500;
      font-size: 40px;
      line-height: 60px;
      color: #3c43cc;

      margin-top: 36px;
      margin-left: 40px;
      @media (max-width: 650px) {
        font-size: 30px;
        line-height: 40px;
      }
    }

    h2 {
      font-weight: 500;
      font-size: 26px;
      line-height: 39px;
      margin-left: 40px;

      @media (max-width: 650px) {
        font-size: 20px;
        line-height: 30px;
      }
    }

    img {
      @media (max-width: 650px) {
        width: 160px;
      }
    }

    button {
      max-width: 200px;
      margin-top: 36px;
      border: 2px solid #000000;
      color: #000;
      border-radius: 6px;
      margin-left: 40px;

      font-weight: 500;
      font-size: 26px;
      line-height: 30px;
      text-transform: none;
      padding: 10px 16px;

      @media (max-width: 650px) {
        font-size: 20px;
        line-height: 30px;
        width: 160px;
        padding: 5px 9px;
      }
    }
  }
}
