.container {
  width: 100%;
  padding: 140px 0 0 0;

  @media (max-width: 650px) {
    height: auto;
    padding: 70px 0;
  }

  .wrapper {
    position: relative;
    width: 100%;
    height: calc(850px - 320px);

    @media (max-width: 650px) {
      height: auto;
      display: flex;
      flex-direction: column;
    }

    .title {
      font-weight: 500;
      font-size: 30px;
      line-height: 35px;

      @media (max-width: 650px) {
        position: inherit;
        left: 0;
      }
    }

    .members {
      padding: 100px 0;
      justify-content: space-between;
      flex-wrap: wrap;

      @media (max-width: 650px) {
        justify-content: center;
        gap: 10px;
      }

      .member {
        width: 20%;

        @media (max-width: 650px) {
          width: 30%;
          overflow: visible;
        }
      }
    }
  }
}
