.container {
  margin: 0 0 100px 0;
  min-height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
  // background: #d9d9d9;

  .title {
    font-weight: 600;
    font-size: 34px;
    line-height: 45px;
    text-align: center;
  }

  .description {
    font-weight: 300;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    color: #5f5f5f;
    max-width: 914px;
    margin-top: 12px;
    margin-bottom: 20px;
  }

  a {
    text-decoration: none !important;
  }

  button {
    background: #ffffff;
    border: 5px solid #3c43cc;
    color: #000;
    min-width: 300px;
    text-decoration: none;

    &:hover,
    &.copied {
      border: 5px solid #3c43cc;
      background-color: #3c43cc;
      color: #fff;
    }
  }
  a {
    margin-top: 10px;
  }
}
