.container {
  background-color: #fff !important;
  color: #000 !important;
  box-shadow: none !important;
  position: fixed !important;
  top: 0 !important;
  z-index: 9999 !important;
  padding: 0 !important;

  @media (max-width: 650px) {
    position: fixed !important;
  }

  .logo {
    font-size: 40px;
    font-weight: 700;

    @media (max-width: 650px) {
      font-size: 24px;
      font-weight: 700;
    }
  }

  .wrapper {
    display: flex;
    height: 120px;

    @media (max-width: 650px) {
      height: 80px;
    }

    .toolbar {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0;

      img {
        width: 194px;
        height: 60px;

        @media (max-width: 650px) {
          width: 120px !important;
          height: 40px !important;
        }
      }

      .link {
        color: #000;
        font-weight: 500;
        font-size: 20px;
        line-height: 23px;
        text-transform: none;

        .active p {
          border-bottom: 4px solid #3c43cc;
        }
        p {
          font-weight: 500;
          font-size: 20px;
          line-height: 23px;
          border-bottom: 4px solid transparent;
          padding-bottom: 10px;
          transition: 0.4s ease-in-out all;
        }
      }
    }
  }

  .hamburger-lines {
    display: block;
    height: 20px;
    width: 24px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .line {
      display: block;
      height: 2.5px;
      width: 100%;
      border-radius: 10px;
      background: #0e2431;
    }
    .line1 {
      transform-origin: 0% 0%;
      transition: transform 0.4s ease-in-out;
    }

    .line2 {
      transition: transform 0.2s ease-in-out;
    }

    .line3 {
      transform-origin: 0% 100%;
      transition: transform 0.4s ease-in-out;
    }

    &.acitve {
      .line1 {
        transform: rotate(45deg) !important;
      }

      .line2 {
        transform: scaleY(0) !important;
      }

      .line3 {
        transform: rotate(-45deg) translate(0px, -1px) !important;
        width: 101%;
      }
    }
  }
}
