.container {
  margin: 100px 0;

  .title {
    font-weight: 500;
    font-size: 30px;
    line-height: 35px;
  }

  .works {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 0px;
    margin-top: 30px;

    div {
      display: flex;
      justify-content: center;
      margin: 35px 0;

      @media (max-width: 650px) {
        width: 45%;
        justify-content: flex-start;
        padding-left: 15px;
      }
    }

    img {
      object-fit: contain;
    }
  }
}
