.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  cursor: pointer;

  .img {
    position: relative;

    @media (max-width: 650px) {
      display: flex !important;
      justify-content: center;
      height: 170px;
    }

    .first {
      opacity: 1;

      @media (max-width: 650px) {
        width: 100px !important;
        left: 20%;
      }
    }

    .second {
      opacity: 0;

      @media (max-width: 650px) {
        width: 100px !important;
        left: 20%;
      }
    }

    &:hover {
      .first {
        opacity: 0;
      }

      .second {
        opacity: 1;
      }
    }
    img {
      position: absolute;
      left: 0;
      top: 0;
      transition: 0.4s ease-in-out all;

      @media (max-width: 650px) {
        width: 100px;
      }
    }
  }

  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100%;
    padding-right: 0;

    .name {
      font-weight: 500;
      font-size: 26px;
      line-height: 36px;
      text-transform: uppercase;
      text-align: center;

      @media (max-width: 650px) {
        font-size: 18px;
        line-height: 24px;
        text-align: center;
      }
    }

    .description {
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      max-width: 100px;
      margin-top: 20px;
      margin: 0;
      text-align: center;
      color: rgb(107, 107, 107);
    }
  }

  .tooltip {
    position: absolute;
    padding: 20px 25px;
    min-width: 269px;
    min-height: 159px;
    background-image: url('../../../assets/icons/dial.svg');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    border-radius: 6px;
    left: -120px;
    top: -150px;
    opacity: 0;
    transition: 0.4s ease-in-out all;
    display: flex;
    align-items: center;
    justify-content: center;

    @media (max-width: 650px) {
      z-index: 99;
      max-width: 300px;
      min-height: 159px;
      min-width: auto;
      left: 0;
      top: -100px;
    }

    .title {
      font-weight: 600;
      font-size: 24px;
      line-height: 32px;
      color: #000;
      margin-bottom: 10px;

      @media (max-width: 650px) {
        font-size: 12px;
        line-height: 18px;
        max-width: 200px;
      }
    }

    &.top-left1 {
      left: -160px;
      top: -100px;

      @media (max-width: 650px) {
        left: -40px;
        top: -80px;
      }
    }

    &.top-left2 {
      left: -120px;
      top: -180px;

      @media (max-width: 650px) {
        left: -40px;
        top: -100px;
      }
    }

    &.top-left3 {
      left: -220px;
      top: -100px;

      @media (max-width: 650px) {
        left: -50px;
        top: -30px;
      }
    }

    &.top-right {
      top: -160px;
      left: 80px;
      background-image: url('../../../assets/icons/dial2.svg');

      @media (max-width: 650px) {
        left: 40px;
        top: -80px;
      }

      &::after {
        top: 99%;
        left: 40%;
        transform: rotateZ(357deg);
      }
    }

    &.bottom-right {
      left: 22%;
      top: calc(100% + 30px);

      @media (max-width: 650px) {
        left: 100px;
      }

      &::after {
        top: auto;
        bottom: 98%;
        left: 20%;
        transform: rotateZ(246deg);
      }
    }

    &.bottom-left {
      left: -120px;
      top: calc(100% + 30px);

      @media (max-width: 650px) {
        left: 0;
      }

      &::after {
        top: auto;
        bottom: 98%;
        left: auto;
        right: 20%;
        transform: rotateZ(173deg);
      }
    }
  }

  &:hover {
    .img {
      img {
        filter: grayscale(0);
      }
    }

    .tooltip {
      opacity: 1;
    }
  }
}
