.container {
  position: relative;
  display: flex;
  padding-top: 5px;
  height: 100%;

  &:hover {
    .list {
      display: block;
    }
  }

  .icon {
    cursor: pointer;
  }

  .list {
    position: absolute;
    top: 100%;
    left: -13px;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.1);
    border-radius: 7px;
    overflow: hidden;
    display: none;

    .item {
      padding: 10px 15px;
      transition: 0.4s ease-in-out all;
      cursor: pointer;
      border-radius: 7px;

      &.active,
      &:hover {
        box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.1);
        color: #3c43cc;
      }
    }
  }
}
